<template>
    <scroll class="suggest" :data="result" :pullup="pullup" @scrollToEnd="searchMore" :beforeScroll="beforeScroll" @beforeScroll="listScroll" ref="suggest">
        <ul class="suggest-list">
            <li @click="selectItem(item)" class="suggest-item" v-for="item in result">
                <div class="icon">
                    <i :class="getIconCls(item)"></i>
                </div>
                
                <div class="name">
                    <p class="text" v-html="getDisplayName(item)"></p>
                </div>
            </li>
            <loading v-show="hasMore" title=""></loading>
        </ul>
        <div v-show="!hasMore && !result.length" class="no-result-wrapper">
            <no-result title="抱歉，暂无搜索结果"></no-result>
        </div>
    </scroll>
</template>

<script type="text/ecmascript-6">
import {search} from 'api/search'
import {ERR_OK} from 'api/config'
import {createSong} from 'common/js/_song'
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import Singer from 'common/js/singer'
import {mapMutations, mapActions} from 'vuex'
import { getPlayVkey } from 'api/singer'
import NoResult from 'base/no-result/no-result'

const TYPE_SINGER = 'singer'
const perpage = 20

    export default {
        props: {
            query: {
                type: String,
                default: ''
            },
            showSinger: {
                type: Boolean,
                default: true
            }
        },
        data(){
            return {
                page: 1,
                result: [],
                pullup: true,
                hasMore: true,
                vkey: '',
                beforeScroll: true
            
            }
        },
        methods: {
            search() {
                this.page = 1
                this.hasMore = true
                this.$refs.suggest.scrollTo(0,0)
                search(this.query,this.page,this.showSinger,perpage).then((res) => {
                    if(res.code === ERR_OK){
                        this.result = this._normalizeSongs(res.data.song.list)
                       // console.log(this.result)
                        this._checkMore(res.data)
                        //console.log(res.data)
                    }
                })
            },
            searchMore() {
                if(!this.hasMore) {
                    return
                }
                this.page++
                search(this.query,this.page,this.showSinger,perpage).then((res) => {
                    if(res.code === ERR_OK){
                        
                        this.result =this._normalizeSongs(res.data.song.list)
                      // console.log(res.data)
                     // console.log(this.result)
                        this._checkMore(res.data)
                       
                    }
                })
                //console.log(this.result)
            },
            _checkMore(data){
                const song = data.song
                if( (song.curnum+song.curpage * perpage) >=song.totalnum){
                    this.hasMore = false
                }
            },
            /*_genResult(data) {
                let ret = []
                //console.log(ret)
                if(data.zhida && data.zhida.singerid){
                    ret.push({...data.zhida, ...{type: TYPE_SINGER}})
                   // console.log(ret)
                }
                if(data.song) {
                    ret = ret.concat(this.normalizeSongs(data.song.list))
                   console.log(ret)
                }
                console.log(ret)
                return ret
            },*/
            getIconCls(item) {
                if(item.singer === TYPE_SINGER) {
                    return 'icon-mine'
                }else{
                    return 'icon-music'
                }
            },
            getDisplayName(item) {
                if(item.type === TYPE_SINGER) {
                    return item.singername
                }else{
                    return `${item.name}-${item.singer}`
                }
            },
             _normalizeSongs(list) {
            let ret = []
            list.forEach((musicData) => {

                getPlayVkey(musicData.songmid).then((res) => {
                    if (res.code === ERR_OK) {
                        this.vkey = res.data.items[0].vkey
                        //this.url = `http://dl.stream.qqmusic.qq.com/C400${musicData.songmid}.m4a?vkey=${this.vkey}&guid=381496156&uin=0&fromtag=66`
                         //console.log(this.url)
                        
                    }                  
                      if (musicData.songid && musicData.albummid) {                        
                            ret.push(createSong(musicData,this.vkey))
                            // console.log(ret)
                        }                                    
                })     

            })
           
            //console.log(ret)
            return ret             
        },

        selectItem(item) {
            if(item.type === TYPE_SINGER) {
                const singer = new Singer({
                    id: item.singermid,
                    name: item.singername
                })
                this.$router.push({
                    path: `/search/${singer.id}`
                })
                this.setSinger(singer)
             }else {
                this.insertSong(item)
                }
                //this.$emit('select', item)
          },
          listScroll() {
            this.$emit('listScroll')
          },
        ...mapMutations({
            setSinger: 'SET_SINGER'
        }),
        ...mapActions([
            'insertSong'
        ])
        },
        watch: {
            query() {
                this.search()
            }
        },
        components: {
            Scroll,
            Loading,
            NoResult
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 0 30px
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: $color-text-d
      .name
        flex: 1
        font-size: $font-size-medium
        color: $color-text-d
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>


